.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body{
  background-color:bisque;
}
ul {
width: 60%;
margin: auto;
list-style-type: none;

}
li{
  background-color: rgb(174, 138, 173);
}
li:nth-child(even) {
  background-color: rgb(65, 59, 65);
  color: white;
}

.custom-color{
  background-color:rgb(219, 159, 217);
  border: 2px solid rgb(65, 59, 65);
}

.custom-color:hover{
  background-color:rgb(65, 59, 65);
  border: 2px solid rgb(219, 159, 217);
}

.custom-color:focus{
  background-color:rgb(65, 59, 65);
  border: 2px solid rgb(219, 159, 217);
}




